.form-container{
    max-width: 800px;
    margin: auto;
}
.error-section{
    color:red;

}
.error-section::after{
    content: " *";
}
.toggle-password-visibility{
    font-size: .9em;
    color: gray;
    width: fit-content;
    cursor: grab;
}
.navigate-forms{
    font-size: .9em;
    padding: 1px 6px;
    margin: 0;
}
.account-access-google{
    display: flex;
    align-items: center;
    gap: 8px;
    justify-content: center;
    font-size: 1em;
    background-color: #e3e3e3;
    border: none;
    border-radius: 12px;
    padding: 12px;
    cursor: pointer;
    font-weight: 500;
}