#empty-cart-section{
    text-align: center;
}
.empty-cart-message{
    font-weight: 500;
    margin-bottom: 0;
}
.add-items{
    text-align: center;
}
#cart-section{
    margin: auto;
    max-width: 1300px;
}
#products-container article{
    display: flex;
    align-items: center;
    gap: 15px;
    background-color: #e3e3e3;
    border-radius: 12px;
    padding: 15px;
    margin: 15px 0;
    transition: 0.35s ease-in-out;
}
#products-container article:hover{
    background-color: #d5d5d5;
}
#products-container article img{
    max-height: 100px;
    max-width: 100px;
}
.cart-total-details, #profile-details{
    margin-bottom: 12px;
}