#product-details{
    display: grid;
    grid-template-columns: 1fr 2fr;
}
.product-details-img, .product-preview-img{
    display: flex;
    align-items: center;
    height: 100%;
}
.product-preview-img{
    align-items: flex-start !important;
}
.product-details-img-container, .product-preview-img-container{
    width: 400px;
    height: 400px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
    margin-left: auto;
    margin-right: auto;
}
.product-preview-img-container{
    width: 97% !important;
    height: 97% !important;
}
.product-details-img-container img, .product-preview-img-container img{
    max-width: 400px;
    max-height: 400px;
    cursor: pointer;
}
.product-preview-img-container img{
    max-width: 97% !important;
    max-height: 97% !important;
    cursor: default;
    background-color: white;
}
#product-table{
    width: 25%;
}
#product-table td {
    border: 1px solid;
    padding: 4px;
}
.product-attributes{
    font-weight: bold;
    min-width: 33%;
}
.product-attributes::after{
    content: ":";
}
#photo-preview-heading{
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
#photo-preview-zoomed{
    position: fixed;
    background-color: #e3e3e3;
    padding: 15px;
    top: 50%; left: 50%; 
    transform: translate(-50%, -50%);
    /* subtract 5% worth of space due to main container taking 5% padding already*/
    width: 90%;
    height: 75%;
}
#photo-preview-heading button{
    background: none;
    border: none;
    font-weight: bold;
    cursor: pointer;
    font-size: 1em;
    color: red;
}
@media (max-width: 1500px) {
    .product-details-img-container{
        width: 300px;
        height: 300px;
    }
    .product-details-img-container img{
        max-width: 300px;
        max-height: 300px;
    }
}
@media (max-width: 750px) {
    #product-details{
        display: block;
    }
    .product-details-img-container{
        width: 300px;
        height: 300px;
    }
    .product-details-img-container img{
        max-width: 300px;
        max-height: 300px;
    }
}
@media (max-width: 425px) {
    .product-details-img-container{
        width: 230px;
        height: 230px;
    }
    .product-details-img-container img{
        max-width: 230px;
        max-height: 230px;
    }
}