nav{
    display: flex;
    padding: 30px 5%;
    align-items: center;
    background: #1f1f1f;
    position: fixed;
    width: 100%;
    /* below allows no component to overlap with the navigation bar */
    z-index: 1;
}
.hamburger{
    color: #848484;
    font-size: 1.5em;
    display: none;
    cursor: pointer;
    position: absolute;
    right: 5%;
}
#nav-links{
    align-items: center;
    gap: 25px;
}
#nav-logo{
    margin-right: auto;
    font-size: 1.5em;
    font-weight: 400;
    color: #c8c8c8;
    padding: 0;
}
.nav-link{
    color: #848484;
    font-weight: 600;
    padding: 6px 9px;
    border-radius: 2px;
    display: flex;
    align-items: center;
    gap: 5px;
}
#nav-links .nav-link:hover{
    background-color: rgba(255, 255, 255, 0.111);
}
@media (max-width: 800px) {
    nav{
        flex-direction: column;
        gap: 25px;
    }
    #nav-links{
        flex-direction: column;
        width: 100%;
        gap: 0;
    }
    .nav-link{
        padding: 25px 100%;
    }
    .hamburger{
        display: block;
    }
}
@media (min-width: 800px) {
    #nav-links{
        display: flex !important;
    }
}