*{
    box-sizing: border-box;
    font-family: Inter;
    scroll-behavior: smooth;
}
a{
    text-decoration: none;
    color: black;
}
main{
    min-height: 50vh;
    padding: 40px 5%;
}
.heading{
    font-weight: 500;
    color: black;
    margin: 8px 0;
}
.classical-cta{
    width: 100%;
    font-size: 1em;
    background-color: #0955e5;
    border: none;
    color: white;
    padding: 12px;
    border-radius: 12px;
    font-weight: 600;
    cursor: pointer;
    text-align: center;
}
.classical-cta:hover{
    background-color: #0749c2;
}
.classical-link{
    color: #0749c2;
    text-decoration: none;
    font-weight: 500;
}
.danger-cta{
    width: 100%;
    font-size: 1em;
    background-color: red;
    border: none;
    color: white;
    padding: 12px;
    border-radius: 12px;
    font-weight: 600;
    cursor: pointer;
    text-align: center;
}
.danger-cta:hover{
    background-color: rgb(178, 0, 0);
}
.understated-text{
    font-size: .7em !important;
    font-weight: normal;
}
.bolded-text{
    font-weight: 600;
}
.unstyled-btn{
    border: none;
    background: none;
}
.grab{
    cursor: grabbing;
}